import React from "react";
import { Button, Result } from "antd";
import { useNavigate } from "react-router-dom";

function NotFound() {
  const navigate = useNavigate();

  return (
    <Result
      status="404"
      title="404"
      subTitle=",     !"
      extra={
        <Button
          type="primary"
          onClick={() => {
            navigate(-1);
          }}
        >
          
        </Button>
      }
    />
  );
}

export default NotFound;
